.footer {
    padding-bottom: 72px;
    @include sm {
        padding-bottom: 40px; }
    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include sm {
            flex-direction: column;
            align-items: flex-start; } }
    &__social {
        @include sm {
            margin-bottom: 30px; }
        ul {
            display: flex;
            li {
                margin-right: 12px;
                &:last-child {
                    margin-right: 0; } } }
        .icon {
            font-size: 37px; } }
    &__nav {
        ul {
            display: flex;
            color: #fff;
            font-size: 18px;
            li {
                margin-right: 40px;
                &:last-child {
                    margin-right: 0; } } } } }
