.works {
    position: relative;
    padding-bottom: 113px;
    overflow: hidden;
    @include md {
        padding-bottom: 72px; }
    &__content {
        padding: 78px 100px 85px;
        background-color: #fff;
        border-radius: 16px;
        @include md {
            padding: 33px 36px 50px; }
        @include sm {
            padding: 29px 13px 50px;
            border-radius: 8px; }
        .section__title {
            margin-bottom: 26px;
            @include md {
                margin-bottom: 34px; }
            @include sm {
                padding-left: 7px;
                margin-bottom: 24px; } } }
    &__list {
        columns: 2;
        column-gap: 73px;
        font-weight: 300;
        @include md {
            column-gap: 16px; }
        @include sm {
            columns: auto; } }
    &__item {
        position: relative;
        display: inline-block;
        margin-bottom: 36px;
        font-size: 18px;
        line-height: 2;
        @include md {
            font-size: 14px;
            line-height: 1.5; }
        @include sm {
            line-height: 1.6;
            margin-bottom: 21px; }
        &:nth-child(3n) {
            margin-bottom: 0;
            @include sm {
                margin-bottom: 21px; } } }
    &__img {
        position: absolute;
        width: 48px;
        top: 10px;
        left: 0;
        @include md {
            top: 4px; } }
    &__text {
        padding-left: 80px;
